.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80%;
  color: wheat;
}

.main-container {
  padding: 6rem 1.2rem;

  h1 {
    font-size: 7.5rem;
    font-weight: 200;
    opacity: 0;
    animation-delay: 1s;
    animation: fadeInUp 2s ease-in-out 0s forwards;

    span {
      font-size: 7.5rem;
    }
  }

  span.description {
    display: flex;
    font-size: 1.6rem;
    color: cadetblue;
    font-weight: 900;
    padding: 1rem 3rem;
    background: #343d48;
  }
}

.links-container a {
  display: inline-block;
  width: 7rem;
  height: 7rem;
  margin: 1rem;
  border-radius: 50%;
  position: relative;
  background: #cc4a5c;
  z-index: 10;
  cursor: pointer;
  text-indent:-9999px;
  white-space: nowrap;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    border-radius: 50%;
    background: #cc4a5c;
    height: 100%;
  }
  &:hover:before {
    animation: fadeOut 1s ease-in-out;
  }
  &:hover {
    transform: rotate(360deg);
    background: #063956;
    transition: 0.3s all linear;
  }
  span {
    display: none;
  }
}


#linkedin {
  background-image: url("public/linkedIn.jpg");
  background-size: 60%;
  background-position: center;
  background-repeat: no-repeat;
}
#github {
  background-image: url("public/github.png");
  background-size: 80%;
  background-position: center;
  background-repeat: no-repeat;
}
#stackoverflow {
  background-image: url("public/stackoverflow.png");
  background-size: 80%;
  background-position: center;
  background-repeat: no-repeat;
}

#twitter {
  background-image: url("public/twitter.png");
  background-size: 80%;
  background-position: center;
  background-repeat: no-repeat;
}
#fiverr {
  background-image: url("public/fiverr.png");
