}

.skills-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  .skill-cont {
    border: 2px solid red;
    color: red;
  }
  .color-dot {
    position: absolute;
    background: red;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }

  50% {
    -webkit-transform: scale(1.2, 1.2);
    transform: scale(1.2, 1.2);
  }
  100% {
    opacity: 0;
    z-index: 1;
  }
}

@media only screen and (max-width: 700px) {
  .main-container {
    span.description {
      line-height: 2.4rem;
      font-size: 2rem;
    }
  }
  .links-container a {
    width: 8rem;
    height: 8rem;
    margin: 0.5rem;
  }
}
